<template>
    <div v-show="!isSingleMode">
        <div class="table-box m-b-10 p-b-5" v-loading="listLoading">
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item :label="$t('label.dockerStatus')">
                    <span class="c-success bold m-r-10" v-if="dockerAvailable">{{$t('global.running')}}</span>
                    <span class="c-danger bold m-r-10" v-else>{{$t('global.stop')}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.swarmStatus')" v-show="dockerAvailable">
                    <span class="c-success bold m-r-10" v-if="swarmAvailable">{{$t('global.running')}}</span>
                    <span class="c-danger bold m-r-10" v-else>{{$t('global.stop')}}</span>
                </el-form-item>
            </el-form>
        </div>
        <div  class="pkgStatusContent p-r table-box">
            <div class="search" v-show="isShowSearchInput"> 
                <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('dockerControl.keywordHolder')" >
                </el-input>
                <i class="fa fa-search search-icon" aria-hidden="true"></i>
            </div>

            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="dockerMarket">
                    <span slot="label">
                        <i class="fa fa-sellsy m-r-5" aria-hidden="true"></i>
                        {{$t('dockerControl.dockerMarket')}}
                        <el-tooltip class="item" effect="light" :content="$t('dockerControl.dockerDeployTooltip')" placement="top-start" >
                            <i class="fa fa-question-circle tooltip  m-l-5" aria-hidden="true"></i>
                        </el-tooltip>
                    </span>
                    <el-table
                        :data="dockerTableData.filter(data => !keywords || data.projectname.toLowerCase().includes(keywords.toLowerCase()))"
                        tooltip-effect="dark"
                        style="width: 100%"
                        v-loading="listLoading"
                        >

                        <el-table-column
                        label=""
                        width="100"
                        >
                            <template slot-scope="scope">
                                <div>
                                    <img src="@/assets/imgs/docker.png"  alt="no img" style="width:48px"/>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        prop="projectname"
                        :label="$t('dockerControl.projectName')"
                        min-width="120"
                        sortable
                        >
                        </el-table-column>

                        <el-table-column
                        prop="version"
                        :label="$t('dockerControl.version')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                        prop="ftype"
                        :label="$t('dockerControl.type')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.view')"
                            min-width="100">
                            <template slot-scope="scope"> 
                                <div>
                                    <i class="fa fa-eye pointer c-primary" aria-hidden="true" @click="showYmlContent(scope.row)" v-if="scope.row.type != 'uninstall'"></i>
                                    <i class="fa fa-eye pointer c-darkgray" aria-hidden="true" @click="showDeviceYmlContent(scope.row)" v-if="scope.row.type === 'uninstall'"></i>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.status')"
                            min-width="100">
                            <template slot-scope="scope"> 
                                <div>
                                    <i class="fa fa-list-ul pointer c-primary" aria-hidden="true" @click="showDockerList(scope.row.dockerData)"></i>
                                </div>
                            </template>
                        </el-table-column>

                        <el-table-column
                        prop="type"
                        :label="$t('dockerControl.action')"
                        min-width="120"
                        :filters="[{ text: $t('dockerControl.uninstall'), value: 'uninstall' }, { text: $t('dockerControl.install'), value: 'install' }, { text: $t('dockerControl.upgrade'), value: 'upgrade' }]"
                        :filter-method="filterType"
                        filter-placement="bottom-end"
                        >
                            <template slot-scope="scope">
                                <div>
                                    <span>
                                        <el-button-group v-if="scope.row.ftype== 'Swarm'">
                                            <el-button 
                                            size="small" 
                                            type="danger" 
                                            @click="selectBatchinstall('uninstallSwarm', scope.row)"
                                            v-if="scope.row.type === 'uninstall'"
                                            :disabled="!swarmAvailable"
                                            >
                                                {{$t('dockerControl.uninstall')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="warning" 
                                            @click="selectBatchinstall('upgradeSwarm', scope.row)" 
                                            v-if="scope.row.type === 'upgrade'"
                                            :disabled="!swarmAvailable"
                                            >
                                                {{$t('dockerControl.upgrade')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="primary" 
                                            @click="selectBatchinstall('installSwarm', scope.row)" 
                                            v-if="scope.row.type === 'install'"
                                            :disabled="!swarmAvailable"
                                            >
                                                {{$t('dockerControl.install')}}
                                            </el-button>
                                        </el-button-group>
                                        <el-button-group v-else>
                                            <el-button 
                                            size="small" 
                                            type="danger" 
                                            @click="selectBatchinstall('uninstall', scope.row)"
                                            v-if="scope.row.type === 'uninstall'"
                                            :disabled="!dockerAvailable&&swarmAvailable"
                                            >
                                                {{$t('dockerControl.uninstall')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="warning" 
                                            @click="selectBatchinstall('upgrade', scope.row)" 
                                            v-if="scope.row.type === 'upgrade'"
                                            :disabled="!dockerAvailable&&swarmAvailable"
                                            >
                                                {{$t('dockerControl.upgrade')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="primary" 
                                            @click="selectBatchinstall('install', scope.row)" 
                                            v-if="scope.row.type === 'install'"
                                            :disabled="!dockerAvailable&&swarmAvailable"
                                            >
                                                {{$t('dockerControl.install')}}
                                            </el-button>
                                        </el-button-group>
                                    </span>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>

                <el-tab-pane name="dockerStatusHistory">
                    <span slot="label">
                        <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                        {{$t('dockerControl.dockerDeployHistory')}}
                    </span>
                    <docker-history
                    :statusType="batchDockerType "
                    ref="dockerHistory"
                    @getStatusCount="getBatchAppStatusCount"
                    :groupname="groupname"
                    @isFinished="isFinished"
                    >
                    </docker-history>

                </el-tab-pane>
            </el-tabs>
            <el-dialog :visible.sync="dialogTasknameVisible" @close="initDockerInfo()" :close-on-click-modal="false">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-level-up m-r-10" ></i>
                    {{$t('dockerControl.batchInstall')}}
                </div>
                <el-form :model="dockerStatusForm" :rules="rules" ref="dockerStatusForm">
                    <el-form-item :label="$t('label.taskName')" label-width="230px" prop="taskname">
                        <el-input v-model.trim="dockerStatusForm.taskname" autocomplete="off" class="max-w-350" :placeholder="$t('dockerControl.taskName')"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogTasknameVisible = false">{{$t('global.cancel')}}</el-button>
                    <el-button type="primary" @click="batchinstall()">{{$t('global.confirm')}}</el-button>
                </div>
            </el-dialog>

            <el-dialog  :visible.sync="dialogDockerVisible">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-list-ul m-r-10" aria-hidden="true"></i>
                    {{$t('dockerControl.status')}}
                </div>
                <el-table :data="dockerData">
                    <el-table-column property="container" :label="$t('dockerControl.container')">
                        <template slot-scope="scope">
                            <div>
                                {{scope.row.container| none}}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column property="image" label="image"></el-table-column>
                    <el-table-column property="version" :label="$t('dockerControl.version')">
                        <template slot-scope="scope">
                            <span>
                                {{scope.row.version|none}}
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column property="isRunning" :label="$t('dockerControl.status')" align="center">
                        <template slot-scope="scope">
                            <span v-if="scope.row.isRunning" :class="{'c-success': scope.row.isRunning=='running'}">{{scope.row.isRunning}}</span>
                            <span v-else>No Ready</span>
                        </template>
                    </el-table-column>
                </el-table>
            </el-dialog>

            <el-dialog  :visible.sync="dialogYamlVisible">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-file-text m-r-10" aria-hidden="true"></i>
                    {{$t('dockerControl.fileContent')}}
                </div>
                <div class="text-center" v-loading="yamlLoading">
                    <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"
                    :options="{
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true,
                    tabSize:2,
                    fontSize:14,
                    showPrintMargin:false,   //?????????
                    }"></editor>
                </div>
                
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogYamlVisible = false" size="small">{{$t('global.cancel')}}</el-button>
                </div>
            </el-dialog>
            <div class="appStatusCount" v-show="isShowOverview">
                <span>{{$t('label.taskName')}} {{docker_taskname}}</span>
                <span>{{$t('dockerControl.total')}} {{total}}</span>
                <span>{{$t('dockerControl.deploying')}} {{installing}}</span>
                <span>{{$t('dockerControl.success')}} {{success}}</span>
                <span>{{$t('dockerControl.failed')}} {{failed}}</span>
            </div>
        </div>
    </div>
    
</template>

<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .pkgStatusContent{
        position: relative;
        .appStatusCount{
            position: absolute;
            top: 10px;
            right: 6px;
            span{
                font-weight: 500;
                font-size: 11px;
                &:nth-child(1){
                    color: $primary-color;
                }
                &:nth-child(2){
                    border-left: 2px solid #ddd;
                    padding-left: 3px; 
                    color: $info-color;
                }
                &:nth-child(3){
                    color: $warn-color;
                }
                &:nth-child(4){
                    color: $success-color;
                }
                &:nth-child(5){
                    color: $darkgray-color;
                }
            }
            
        }
    }

    .search{
        position: absolute;
        top: 23px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

</style>

<script>
    import {dockerControl, dockerAction, deviceInfo} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getRepoDockerApi, getYmlContentApi} from '../../../components/restfulapi/getRepoDockerApi';
    import{getAppStatusOverviewApi} from "@/components/restfulapi/appStatusApi"

    import {getDeviceStatus, setDeviceStatus, batchExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import {singleDockerType, batchDockerType} from "../../../assets/js/constant";
    import dockerHistory from "./history"
    export default{
        name: 'batchdockerControl',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "dockerMarket",
                dockerTableData: [],
                dockerData: [],
                dialogTasknameVisible: false,
                //app status history
                filename: '',
                projectname: '',
                version: '',
                address: '',
                //batch install
                dockerStatusForm:{
                    taskname: '',
                },
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },

                batchDockerType : batchDockerType ,
                cid: "",

                content: "",
                //installstatus count show
                isShowOverview: false,
                docker_taskname: '',
                success: 0,
                installing: 0,
                failed: 0,
                listLoading: false,
                yamlLoading: false,
                keywords: '',

                dialogYamlVisible: false,
                dialogDockerVisible: false,
                swarmAvailable: false,
                dockerAvailable: false
            }
        },

        props:{
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },

        components: {
            dockerHistory,
            editor: require('vue2-ace-editor'),
        },

        methods:{
            getDockerStatus(){
                 if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }

                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, deviceInfo["deviceCapacity"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let dockerinfo = JSON.parse(res.content.value);
                                this.dockerAvailable = dockerinfo.docker == "true"? true: false;
                                this.swarmAvailable = dockerinfo["docker.swarm"] == "true"? true: false;
                            }
                            
                            
                        }
                    })   
                })
            },
            handleCommand(command){
                let appInfo=command.appInfo;
                // console.log(appInfo);
                this.cid = command.id;
                //package is diff in system and third
    
                this.address = appInfo.address;
                this.filename = appInfo.filename;
                this.projectname = appInfo.projectname;
                this.version = appInfo.version;
                this.dockerStatusForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

            filterType(value, row){
                return row.type === value ;
            },

            getSensorStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }
                let dockerManagementInfoArray = [];
                let deviceDockerArray = [];
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, dockerControl["getDockerInfo"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let packageinfo = JSON.parse(res.content.value);
                                let allDockerData =[];
                                if(packageinfo){
                                    allDockerData = packageinfo.data;
                                }
                                if(Object.prototype.toString.call(allDockerData) === "[object Array]" ){
                                    allDockerData.forEach((val) => {
                                        let uninstallObj = {
                                            type: 'uninstall',
                                            filename: "",
                                            projectname: val.tag,
                                            version: val.version,
                                            address: "",
                                            dockerData: val.docker,
                                        };
                                        dockerManagementInfoArray.push(uninstallObj);
                                        deviceDockerArray.push(uninstallObj);                            
                                            
                                    })
                                }
                            }
                            
                            this.getRepoDockers(dockerManagementInfoArray, deviceDockerArray);  
                        }
                        
                          
                    })   
                })
            },
            
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo apps
            getRepoDockers(dockerManagementInfoArray, deviceDockerArray){
                if(
                    Object.prototype.toString.call(dockerManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(deviceDockerArray) != "[object Array]"
                ){
                    console.error("getRepoDockersparameter is not array")
                    return;
                }
                let token;
                let installManagementInfo = {};
                let upgradeManagementInfo = {};
                this.listLoading = true;
                getRepoDockerApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let installopt = "";
                            let upgradeopt = "";
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    installManagementInfo = {
                                        type : "install", 
                                        filename: val.filename, 
                                        projectname: val.tag, 
                                        version: val.version, 
                                        address: val.address,
                                        dockerData: val.repoDockers,
                                        ftype: val.type
                                    };
                                    dockerManagementInfoArray.push(installManagementInfo);
                         
                                    deviceDockerArray.forEach((deviceapp_val, index) => {
                                        if(val.tag === deviceapp_val.projectname){
                                            // pop latest app info
                                            dockerManagementInfoArray.pop();
                                            deviceDockerArray[index].address = val.address;
                                            deviceDockerArray[index].ftype = val.type;
                                            if (_g.cpr_version(val.version, deviceapp_val.version )){
                                                // remove install app  
                                                // this.removeObjInArray(dockerManagementInfoArray, val.projectname);
                                                upgradeManagementInfo = {
                                                    type : "upgrade", 
                                                    filename: val.filename, 
                                                    projectname: val.tag, 
                                                    version: val.version, 
                                                    address: val.address,
                                                    dockerData: deviceapp_val.dockerData,
                                                    ftype: val.type
                                                };
                                                    //push update app 
                                                dockerManagementInfoArray.push(upgradeManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }
                            
                        }
                        this.dockerTableData = dockerManagementInfoArray;
                    })
                    
                })
            },
	
            //batch install app
            batchSetAppSensor(cid, setSensorVal){
                if(dockerControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                _g.openGlobalLoading();
                let pkgdata = {
                    groupname: this.groupname,
                    taskname: this.dockerStatusForm.taskname,
                    filename: this.filename,
                    projectname: this.projectname,
                    version: this.version,
                    downloadpath: setSensorVal,
                }  
                let taskname = this.dockerStatusForm.taskname;
                this.dialogTasknameVisible = false;                  
                batchExecDeviceStatus(dockerControl[cid], pkgdata).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status == "CHANGED"){
                            //show overview
                            setLocal("docker_taskname", taskname);
                            this.activeName = "dockerStatusHistory";
                            this.$refs.dockerHistory.intervalGetDockerStatusHistory();
                        }else{
                            _g.handleError(res);
                        }
                    })
                })                    
            },

            batchinstall(){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.$refs.dockerStatusForm.validate((valid) => {
                    if(valid) {
                        let apkDownloadAddress = this.address;
                        this.batchSetAppSensor(this.cid, apkDownloadAddress);
                        
                    }
                })
            },
            
            selectBatchinstall(type, selectedAppData){
                this.cid = type;
                this.address = selectedAppData.address;
                this.filename = selectedAppData.filename;
                this.projectname = selectedAppData.projectname;
                this.version = selectedAppData.version;
                this.dockerStatusForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

            initDockerInfo(){
                this.filename = "";
                this.projectname = "";
                this.version = "";
                this.address = "";
                this.dockerStatusForm.taskname = "";
                this.cid = "";
            },

             //get updatedp status count
            getBatchAppStatusCount(){
                let docker_taskname = getLocal("docker_taskname");
                if(!docker_taskname){
                    console.error("getBatchAppStatusCount:localStorage(docker_taskname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getAppStatusOverviewApi(docker_taskname, batchDockerType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.docker_taskname = docker_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                            if(this.total > 0 &&  this.activeName === "dockerStatusHistory"){
                                this.isShowOverview = true;
                            }else{
                                this.isShowOverview = false;
                            }
                        }
                    })
                })
            },
            
            initData(){
                this.dockerAvailable = false;
                this.swarmAvailable = false;
                this.dockerTableData = [];
            },

            handleClick(tab){
                if(tab.name === "dockerStatusHistory"){
                    this.getBatchAppStatusCount();
                }else{
                    this.isShowOverview = false;
                }
            },
            isFinished(){
                this.getSensorStatus();  
            },

            editorInit: function () {
                require('brace/ext/language_tools') //language extension prerequsite...
                require('brace/mode/html')                
                require('brace/mode/javascript')    //language
                require('brace/mode/less')
                require('brace/theme/twilight')
                require('brace/snippets/javascript') //snippet
            },

            showYmlContent(info){
                this.address = info.address;
                this.dialogYamlVisible = true;
                this.yamlLoading = true;
                getYmlContentApi(this.address).then((res) => {
                    this.yamlLoading = true;
                    handleResponse(res, (data) => {
                        if(data.status === "success"){
                            this.content = data.data;
                        }
                    })
                })
            },

            showDeviceYmlContent(info){
                this.dialogYamlVisible = true;
                let data = {};
                data.productname = info.projectname;
                data.version = info.version;
                this.yamlLoading = true;
                getDeviceStatus(this.selectedAgentId, dockerAction["getComposeContent"], data).then((obj) => {
                    this.yamlLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                                this.content = res.content.value;
                        }   
                    })   
                })
            },

            showDockerList(dockerData){
                this.dockerData = [];
                this.dockerData = dockerData;
                this.dialogDockerVisible = true;
            }

        },
       
        computed: {
            
            isShowSearchInput(){
                if(this.activeName === "dockerMarket"){
                    return true;
                }else{
                    return false;
                }
            },
            total(){
                return  this.success+ this.installing+ this.failed;
            }

        },
       
        watch: {
            isSingleMode(val){
                if(!val){
                    this.initData();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.initData();
                    this.getSensorStatus();
                    this.getDockerStatus();
                }
            }
        }

    }
</script>


