import {repoApiGet} from '@/assets/js/baseApi'
import {getRepoServerApi} from "../restfulapi/serverConfigApi"
import {getToken} from "./getRepoTokenApi"

let getRepoDockerApi = function(){
    return new Promise((resolve, reject) => {
        getRepoServerApi().then((repoUrl) => {
            let getRepoAppUrl =  repoUrl + "/repoDockerCompose";
            repoApiGet(getRepoAppUrl, "").then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        })
        
    })
}

let getYmlContentApi = function(address){
    let data = {
        address
    }
    return new Promise((resolve, reject) => {
        
        getRepoServerApi().then((repoUrl) => {
            let getRepoAppUrl =  repoUrl + "/dockerComposeYmlContent";
            repoApiGet(getRepoAppUrl, data).then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        })
    
    })
}

export {
    getRepoDockerApi,
    getYmlContentApi
}


