<template>
    <div  v-show="isSingleMode">
        <div class="table-box m-b-10 p-b-5" v-loading="listLoading">
            <el-form :inline="true" class="demo-form-inline">
                <el-form-item :label="$t('label.dockerStatus')">
                    <span class="c-success bold m-r-10" v-if="dockerAvailable">{{$t('global.running')}}</span>
                    <span class="c-danger bold m-r-10" v-else>{{$t('global.stop')}}</span>
                </el-form-item>
                <el-form-item :label="$t('label.swarmSwitch')" v-show="dockerAvailable">
                    <el-switch v-model="swarmAvailable" @change="actionSwarm('switchSwarmCapacity')"></el-switch>
                </el-form-item>
            </el-form>
        </div>
        <div class="p-r table-box">
            <div class="search" v-show="isShowSearchInput"> 
                <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('dockerControl.keywordHolder')" >  
                </el-input>
                <i class="fa fa-search search-icon" aria-hidden="true"></i>
            </div>
            <el-tabs v-model="activeName" @tab-click="handleClick">
                <el-tab-pane name="dockerMarket">
                    <span slot="label">
                        <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                        {{$t('dockerControl.dockerMarket')}}
                        <el-tooltip class="item" effect="light" :content="$t('dockerControl.dockerDeployTooltip')" placement="top-start" >
                            <i class="fa fa-question-circle tooltip m-l-5" aria-hidden="true"></i>
                        </el-tooltip>
                    </span>
                    <el-table
                        :data="dockerTableData.filter(data => !keywords || data.projectname.toLowerCase().includes(keywords.toLowerCase()))"
                        tooltip-effect="dark"
                        style="width: 100%"
                        v-loading="listLoading">

                        <el-table-column
                        label=""
                        width="100"
                        >
                            <template slot-scope="scope">
                                <div>
                                    <img src="@/assets/imgs/docker.png" alt="no img" style="width:48px"/>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                        prop="projectname"
                        :label="$t('dockerControl.projectName')"
                        min-width="120"
                        sortable
                        >
                        </el-table-column>
                        <el-table-column
                        prop="version"
                        :label="$t('dockerControl.version')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                        prop="ftype"
                        :label="$t('dockerControl.type')"
                        min-width="120"
                        sortable>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.view')"
                            min-width="100">
                            <template slot-scope="scope"> 
                                <div>
                                    <i class="fa fa-eye pointer c-primary" aria-hidden="true" @click="showYmlContent(scope.row)" v-if="scope.row.type != 'uninstall'"></i>
                                    <i class="fa fa-eye pointer c-darkgray" aria-hidden="true"  v-if="scope.row.type === 'uninstall'&& scope.row.control == 'Limited'"></i>
                                    <i class="fa fa-eye pointer c-primary" aria-hidden="true" @click="showDeviceYmlContent(scope.row)" v-if="scope.row.type === 'uninstall'&& scope.row.control != 'Limited'"></i>
                                </div>
                            </template>
                        </el-table-column>
                        <el-table-column
                            :label="$t('dockerControl.status')"
                            min-width="100">
                            <template slot-scope="scope"> 
                                <div>
                                    <i class="fa fa-list-ul pointer c-primary" aria-hidden="true" @click="showDockerList(scope.row.dockerData)"></i>
                                </div>
                            </template>
                        </el-table-column>

                        <el-table-column
                        prop="type"
                        :label="$t('dockerControl.action')"
                        min-width="120"
                        :filters="[{ text: $t('dockerControl.uninstall'), value: 'uninstall' }, { text: $t('dockerControl.install'), value: 'install' }, { text: $t('dockerControl.upgrade'), value: 'upgrade' }]"
                        :filter-method="filterType"
                        filter-placement="bottom-end"
                        >
                            <template slot-scope="scope">
                                <div>
                                    <span>
                                        <el-button-group v-if="scope.row.ftype== 'Swarm'">
                                            <el-button 
                                            size="small" 
                                            type="danger" 
                                            @click="appAction('uninstallSwarm', scope.row)"
                                            v-if="scope.row.type === 'uninstall'"
                                            :disabled="!swarmAvailable"
                                            >
                                                {{$t('dockerControl.uninstall')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="warning" 
                                            @click="appAction('upgradeSwarm', scope.row)" 
                                            v-if="scope.row.type === 'upgrade'"
                                            :disabled="!swarmAvailable"
                                            >
                                                {{$t('dockerControl.upgrade')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="primary" 
                                            @click="appAction('installSwarm', scope.row)" 
                                            v-if="scope.row.type === 'install'"
                                            :disabled="!swarmAvailable"
                                            >
                                                {{$t('dockerControl.install')}}
                                            </el-button>
                                        </el-button-group>
                                        <el-button-group v-else>
                                            <el-button 
                                            size="small" 
                                            type="danger" 
                                            @click="appAction('uninstall', scope.row)"
                                            v-if="scope.row.type === 'uninstall'"
                                            :disabled="swarmAvailable||!dockerAvailable"
                                            >
                                                {{$t('dockerControl.uninstall')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="warning" 
                                            @click="appAction('upgrade', scope.row)" 
                                            v-if="scope.row.type === 'upgrade'"
                                            :disabled="swarmAvailable||!dockerAvailable"
                                            >
                                                {{$t('dockerControl.upgrade')}}
                                            </el-button>
                                            <el-button 
                                            size="small" 
                                            type="primary" 
                                            @click="appAction('install', scope.row)" 
                                            v-if="scope.row.type === 'install'"
                                            :disabled="swarmAvailable||!dockerAvailable"
                                            >
                                                {{$t('dockerControl.install')}}
                                            </el-button>
                                        </el-button-group>
                                    </span>
                                </div>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                <el-tab-pane name="dockerStatusHistory">
                    <span slot="label">
                        <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                        {{$t('dockerControl.dockerDeployHistory')}}
                    </span>
                    <docker-history
                    :statusType="singleDockerType"
                    ref="dockerHistory"
                    @isFinished="isFinished"
                    :selectedAgentId="selectedAgentId"
                    >
                    </docker-history>
                </el-tab-pane>
                
            </el-tabs>

            <el-dialog  :visible.sync="dialogDockerVisible">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-list-ul m-r-10" aria-hidden="true"></i>
                    {{$t('dockerControl.status')}}
                </div>
                <el-table :data="dockerData">
                    <el-table-column property="container" :label="$t('dockerControl.container')">
                        <template slot-scope="scope">
                            <div>
                                {{scope.row.container| none}}
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column property="image" label="image"></el-table-column>
                    <el-table-column property="version" :label="$t('dockerControl.version')">
                        <template slot-scope="scope">
                            <span>
                                {{scope.row.version|none}}
                            </span>
                        </template>
                    </el-table-column>
                    <el-table-column property="isRunning" :label="$t('dockerControl.status')" align="center">
                        <template slot-scope="scope">
                            <span v-if="scope.row.isRunning" :class="{'c-success': scope.row.isRunning=='running'}">{{scope.row.isRunning}}</span>
                            <span v-else>No Ready</span>
                        </template>
                    </el-table-column>
                </el-table>
            </el-dialog>
            <el-dialog  :visible.sync="dialogYamlVisible">
                <div slot="title" class="dialog-title">
                    <i class="fa fa-file-text m-r-10" aria-hidden="true"></i>
                    {{$t('dockerControl.fileContent')}}
                </div>
                <div class="text-center" v-loading="yamlLoading">
                    <editor v-model="content" @init="editorInit" lang="html" theme="chrome"  height="550"
                    :options="{
                    enableBasicAutocompletion: true,
                    enableSnippets: true,
                    enableLiveAutocompletion: true,
                    tabSize:2,
                    fontSize:14,
                    showPrintMargin:false,   //?????????
                    }"></editor>
                </div>
                
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogYamlVisible = false" size="small">{{$t('global.cancel')}}</el-button>
                </div>
            </el-dialog>
        </div>
    </div>
    
    
</template>


<script>
    import {dockerControl, dockerAction, deviceInfo} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';
    import {getRepoDockerApi, getYmlContentApi} from '../../../components/restfulapi/getRepoDockerApi';
    import {getSession, checkSession, setSession } from '../../../assets/js/storage';
    import {getRepoSrcApi} from "../../restfulapi/serverConfigApi"
    import {singleDockerType, batchDockerType} from "../../../assets/js/constant";
    import dockerHistory from "./history"

    export default{
        name: 'singledockerControl',
        data(){
            return {
                activeName: "dockerMarket",
                dockerTableData: [],
                dockerData: [],
                //history
                singleDockerType,
                //card
                listLoading: false,
                yamlLoading: false,
                keywords: '',
                content: "",
                dialogYamlVisible: false,
                dialogDockerVisible: false,
                dockerAvailable: false,
                swarmAvailable: false
            }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },
        components: {
            dockerHistory,
            editor: require('vue2-ace-editor'),
        },

        methods:{
            getSensorStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }
                let dockerManagementInfoArray = [];
                let deviceDockerArray = [];
                this.listLoading = true;
                getDeviceStatus(this.selectedAgentId, dockerControl["getDockerInfo"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let packageinfo = JSON.parse(res.content.value);
                                let allDockerData = [];
                                if(packageinfo){
                                    allDockerData = packageinfo.data;
                                }

                                if(Object.prototype.toString.call(allDockerData) === "[object Array]" ){
                                    allDockerData.forEach((val) => {
                                        let uninstallObj = {
                                            type: 'uninstall',
                                            filename: "",
                                            projectname: val.tag,
                                            version: val.version,
                                            address: "",
                                            dockerData: val.docker,
                                            ftype: val.type,
                                            control: val.control
                                        };
                                        dockerManagementInfoArray.push(uninstallObj);
                                        deviceDockerArray.push(uninstallObj);
                                    })
                                    
                                }
                            }
                            
                            this.getRepoDockers(dockerManagementInfoArray, deviceDockerArray);
                        }
                    })   
                })
            },

            getDockerStatus(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }

                this.listLoading = true;
                getDeviceStatus(this.selectedAgentId, deviceInfo["deviceCapacity"]).then((obj) => {
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let dockerinfo = JSON.parse(res.content.value);
                                this.dockerAvailable = dockerinfo.docker == "true"? true: false;
                                this.swarmAvailable = dockerinfo["docker.swarm"] == "true"? true: false;
                            }
                            
                            
                        }
                    })   
                })
            },

            actionSwarm(cid){
                if(this.selectedAgentId == ""){
                    console.error("selectAgentId is empty");
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        let obj = {
                            value: this.swarmAvailable
                        }
                        setDeviceStatus(this.selectedAgentId, dockerControl[cid], JSON.stringify(obj), true).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                        // this.getDockerStatus();                        
                                    })
                                }else{
                                    _g.handleError(res);
                                    this.swarmAvailable = this.swarmAvailable? false: true;
                                }
                            })
                        })
                    }else{
                        this.swarmAvailable = this.swarmAvailable? false: true;
                    }
                }) 
            },
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_apk_val){
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    originData.forEach((obj_val, index) => {
                        if(obj_val.package === rem_apk_val){
                            originData.splice(index ,1)
                        }
                    })
                }else{
                     console.error("data is not array")
                     return;
                }
                
            },
            //get repo dockers
            getRepoDockers(dockerManagementInfoArray, deviceDockerArray){
                if(
                    Object.prototype.toString.call(dockerManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(deviceDockerArray) != "[object Array]"
                ){
                    console.error("getRepoDockersparameter is not array")
                    return;
                }
                let token;
                let installManagementInfo = {};
                let upgradeManagementInfo = {};
                this.listLoading = true;
                getRepoDockerApi().then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    let version = val.versionName != null ? val.versionName:"";
                                    installManagementInfo = {
                                        type : "install", 
                                        filename: val.filename, 
                                        projectname: val.tag, 
                                        version: val.version, 
                                        address: val.address,
                                        dockerData: val.repoDockers,
                                        ftype: val.type
                                    };
                                    dockerManagementInfoArray.push(installManagementInfo);
                         
                                    deviceDockerArray.forEach((deviceapp_val, index) => {
                                        // console.log(val.tag+"##"+deviceapp_val.projectname)
                                        if(val.tag === deviceapp_val.projectname){
                                            // pop latest app info
                                            dockerManagementInfoArray.pop();
                                            deviceDockerArray[index].address = val.address;
                                            deviceDockerArray[index].ftype = val.type;
                                            if (_g.cpr_version(val.version, deviceapp_val.version )){
                                                // remove install app  
                                                // this.removeObjInArray(dockerManagementInfoArray, val.pkgname);
                                                upgradeManagementInfo = {
                                                    type : "upgrade", 
                                                    filename: val.filename, 
                                                    projectname: val.tag, 
                                                    version: val.version, 
                                                    address: val.address,
                                                    dockerData: deviceapp_val.dockerData,
                                                    ftype: val.type
                                                };
                                                    //push update app 
                                                dockerManagementInfoArray.push(upgradeManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }
                            
                        }
                        this.dockerTableData = dockerManagementInfoArray.reverse();
                    })
                    
                })
            },

            filterType(value, row){
                return row.type === value ;
            },
            actionAppSensor(cid, appdata){
                if(dockerControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }

                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        execDeviceStatus(this.selectedAgentId, dockerControl[cid], appdata).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.activeName = "dockerStatusHistory";
                                    this.$refs.dockerHistory.intervalGetDockerStatusHistory();
                                }else{
                                    _g.handleError(res)
                                }
                            })
                        })
                    }
                })
            },

            setAppSensor(cid, setSensorVal){
                if(dockerControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                if(!setSensorVal){
                    console.error("setSensorVal is null")
                    return;
                }
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("dockerControl."+cid), this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) => {
                    if (willfunc) {
                        _g.openGlobalLoading();
                        setDeviceStatus(this.selectedAgentId, dockerControl[cid], setSensorVal).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status == "CHANGED"){
                                    this.$swal("",this.$t('global.success'),"success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getSensorStatus();                        
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                            
                        })
                    }
                })
                
            },

            isFinished(){
                if(this.selectedAgentId){
                    if(!this.isOffline){
                        this.getSensorStatus();

                    }

                }
            },

            appAction(cid, selectedAppData){
                let taskname = this.singleDockerType+ (new Date()).getTime();
                let pkgdata = {
                    taskname,
                    filename: selectedAppData.filename,
                    projectname: selectedAppData.projectname,
                    version: selectedAppData.version,
                    downloadpath: selectedAppData.address,
                }
                this.actionAppSensor(cid, pkgdata)
            },


            initData(){
                this.dockerTableData = [];
                this.dockerAvailable = false;
                this.swarmAvailable = false;
            },
            handleClick(tab){
                if(tab.name === "dockerStatusHistory"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },

            editorInit: function () {
                require('brace/ext/language_tools') //language extension prerequsite...
                require('brace/mode/html')                
                require('brace/mode/javascript')    //language
                require('brace/mode/less')
                require('brace/theme/twilight')
                require('brace/snippets/javascript') //snippet
            },

            showYmlContent(info){
                this.address = info.address;
                this.dialogYamlVisible = true;
                this.yamlLoading = true;
                getYmlContentApi(this.address).then((res) => {
                    this.yamlLoading = false;
                    handleResponse(res, (data) => {
                        if(data.status === "success"){
                            this.content = data.data;
                        }
                    })
                })
            },

            showDeviceYmlContent(info){
                this.dialogYamlVisible = true;
                let data = {};
                data.productname = info.projectname;
                data.version = info.version;
                this.yamlLoading = true;
                getDeviceStatus(this.selectedAgentId, dockerAction["getComposeContent"], data).then((obj) => {
                    this.yamlLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                                this.content = res.content.value;
                        }   
                    })   
                })
            },

            showDockerList(dockerData){
                this.dockerData = [];
                this.dockerData = dockerData;
                this.dialogDockerVisible = true;
            }
        },
        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            selectedAgentId(val){
                if(this.isSingleMode) {
                    this.initData();

                    if(val&& !this.isOffline){
                        this.getSensorStatus();
                        this.getDockerStatus();
                        // this.$refs.dockerHistory.intervalGetDockerStatusHistory();
                    }
                }
            },
           
        },
        computed: {
            isShowSearchInput(){
                if(this.activeName === "dockerMarket"){
                    return true;
                }else{
                    return false;
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

    .search{
        position: absolute;
        top: 23px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
</style>



